export class UtilsService {
	static unique(array: any[]): any[] {
		return array.reduce((accumulator, currentValue) => {
			if (accumulator.indexOf(currentValue) < 0) {
				accumulator.push(currentValue);
			}
			return accumulator;
		}, []);
	}
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
